	module.exports = {"data":{"markdownRemark":{"html":"<blockquote>\n<p>我是張欣嘉，Vibert Thio。這份文件包含我參與 2018《聯覺共振》於國美館的聯展計畫書，與相關硬體需求。</p>\n</blockquote>\n<h2 id=\"論述\"><a href=\"#%E8%AB%96%E8%BF%B0\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>論述</h2>\n<p>弟弟終於進了大學。多少受到我的影響，他選擇了北藝大新媒體藝術學系。也因此，他開始接觸攝影、注意身邊許多被淘汰的科技儀器，包括我們家裡面那台許久沒動的手持 DV 與裝了一鐵盒的 DV 帶。裡面盛裝的是我們的童年，那些黃黃的、畫面模糊的印象。</p>\n<p>大約二十卷左右，最早的拍攝時期是我已經上了小學的年紀，那時候弟弟還在唸幼稚園。因為當時這樣的 DV 已經是最新的影像設備。一天，我與女友在家裡，翻找了一捲捲的錄影帶，看到爸媽年輕的身影，</p>\n<h2 id=\"硬體需求\"><a href=\"#%E7%A1%AC%E9%AB%94%E9%9C%80%E6%B1%82\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>硬體需求</h2>\n<ul>\n<li>架子 * 4</li>\n<li>螢幕 * 2</li>\n<li>管線 * 100</li>\n<li>雙聲道音響系統</li>\n</ul>\n<h2 id=\"初期計畫\"><a href=\"#%E5%88%9D%E6%9C%9F%E8%A8%88%E7%95%AB\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>初期計畫</h2>\n<ul>\n<li>呈現想像中腦袋裡面的運作方式</li>\n<li>記憶裝置都是義肢</li>\n<li>所以需要生物上的元素去維持義肢的功能</li>\n<li>連接一些管線，傳遞物質（可以是光纖、或是水管中裝上 led 燈）</li>\n<li>很多密集的線</li>\n<li>線連到哪裡呢？一台電腦，一個最終極的科技義肢</li>\n<li>我要幾個裝置？我猜四個，dv、記憶卡、硬碟、隨身碟、書</li>\n</ul>","frontmatter":{"title":"聯覺共振：分靈裝置"}}},"pathContext":{"slug":"/docs/2018-01-27-chromesthesia/"}}

